.post {
    display: flex;
    align-items: flex-start;
    border-bottom: 1px solid var(--twitter-background);
    padding-bottom: 10px;
    width: 100%;
}

.post_body {
    flex: 1;
    padding: 10px;
    min-width: 350px;
}
.userPostImg{
    width: 100%;
}

.post_body>img {
    border-radius: 20px;
}

.post_footer {
    display: flex;
    justify-content: space-between;
    padding: 0 10px;
    margin-top: 10px;
}

.post_footer_Icon:hover {
    color: var(--twitter-color);
}

.post_headerDescription {
    margin-bottom: 10px;
    font-size: 15px;
}

.post_headerText>h3 {
    font-size: 15px;
    margin-top: 5px;
}

.post_badge {
    font-size: 14px !important;
    color: var(--twitter-color);
}

.post_headerSpaical {
    font-weight: 600;
    font-size: 12px;
    color: gray;
}

.post_avatar {
    padding: 20px;
}

@media screen and (max-width:1024px) {
    .userPostImg {
        width: 100%;
    }
}
@media screen and (max-width:945px) {
    .userPostImg {
        
    }
}